'use client'

import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
Card,
CardContent,
CardDescription,
CardHeader,
CardTitle,
} from "@/components/ui/card"
import {
Field,
FieldGroup,
FieldLabel,
} from "@/components/ui/field"
import { Input } from "@/components/ui/input"
import { useState } from "react"
import axios from "axios"

export function LoginForm({
className,
...props
}: React.ComponentProps<"div">) {
const [loading, setLoading] = useState(false)
const [error, setError] = useState<string | null>(null)

const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
e.preventDefault()
setLoading(true)
setError(null)

```
const formData = new FormData(e.currentTarget)
const payload = {
  email: formData.get("email"),
  password: formData.get("password"),
}

try {
  const apiUrl = `/api/admin/login` // calls your Next.js API route
  const response = await axios.post(apiUrl, payload, {
    headers: { "Content-Type": "application/json" },
  })

  if (response.data.success) {
    console.log("Login successful!")
    // Optionally redirect user here
  } else {
    setError(response.data.error || "Login failed")
  }
} catch (err: any) {
  setError(err.response?.data?.error || err.message || "Login failed")
} finally {
  setLoading(false)
}
```

}

return (
<div className={cn("flex flex-col gap-6", className)} {...props}> <Card> <CardHeader> <CardTitle>Login to your account</CardTitle> <CardDescription>
Enter your email below to login to your account </CardDescription> </CardHeader> <CardContent> <form onSubmit={handleSubmit}> <FieldGroup> <Field> <FieldLabel htmlFor="email">Email</FieldLabel> <Input
               id="email"
               name="email"
               type="email"
               placeholder="m@example.com"
               required
             /> </Field> <Field> <div className="flex items-center"> <FieldLabel htmlFor="password">Password</FieldLabel> <a
                 href="#"
                 className="ml-auto inline-block text-sm underline-offset-4 hover:underline"
               >
Forgot your password? </a> </div> <Input id="password" name="password" type="password" required /> </Field>
{error && <p className="text-sm text-red-500">{error}</p>} <Field> <Button type="submit" disabled={loading}>
{loading ? "Logging in..." : "Login"} </Button> </Field> </FieldGroup> </form> </CardContent> </Card> </div>
)
}
